  return (
    <div className="flex flex-col items-center justify-center min-h-screen bg-gray-100 p-4">
      <h1 className="text-2xl font-bold mb-4">ðŸ˜‚ Dad Joke Generator</h1>
      <button
        onClick={() => refetch()}
        disabled={isFetching}
        className="px-4 py-2 bg-blue-500 text-white rounded hover:bg-blue-600 transition"
      >
        {isFetching ? "Fetching..." : "Get a Dad Joke"}
      </button>
      {joke && <p className="mt-4 p-4 bg-white shadow-md rounded">{joke}</p>}
    </div>
  );
